nav {
  position: fixed;
  top: 0;
  z-index: 100000;
  width: 100vw;
  background: rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(4px);
}

nav li a:hover {
  color: #f1c30f;
}

.social-Container{
    margin-left: 8.5vw;
}
.social-icon {
  font-size: 1.5rem;
  margin: 0 0.5vw;
}
.dropDownMenu {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.25)!important;
  backdrop-filter: blur(8px)!important;
  -webkit-backdrop-filter: blur(4px)!important;
}

.btnContainer li {
  margin-bottom: 3.6vh;
}

.btnContainer li a {
  font-size: 1.7rem;
  font-weight: 700;
}

@media (max-width: 768px) {

  .btnContainer li a {
    font-size: 1.2rem;
  }
  .social-Container{
    margin-left: -5vw;
}
  .social-icon{
    font-size: 1.2rem;
  }
  .contact{
    display: none;
  }
}
